import { Box } from "@mui/system";
import { Typography } from "@mui/material";

function Title() {
    return (
        <>
            <Box sx = {{textAlign: "center", paddingTop: "40px"}} >
                <Typography variant="h2" sx = {{fontSize: "24px", textTransform:"uppercase", paddingBottom: "10px", fontWeight: "bold"}}> H thng bo him x hi 1 ln </Typography>
                <Typography variant="subtitle" sx = {{fontStyle: "italic"}} component="div" >Cch tnh bo him x hi 1 ln n gin, dnh cho ngi mun t tnh tin BHXH 1 ln</Typography>
            </Box>
        </>
    )
}
export default Title;