import { dataNewSelector } from "../Selector/selector";
import { useSelector } from "react-redux";
import { Typography, Box } from "@mui/material";
import "../App.css";
function ExplainStage() {
  const listNewData = useSelector(dataNewSelector);
  console.log(listNewData);
  return (
    <>
      {listNewData.map((item, index) => (
        <Box key={index}>
          <Box>
            {item.yearNext > item.year ? (
              <>
                <Box>
                  Giai on ng bo him x hi t:{" "}
                  <Typography variant="span" className="text-data">
                    T{item.month} n T12 nm {item.year}. Thi gian{" "}
                    {item.stageMonth} thng
                  </Typography>{" "}
                </Box>
                <Box>
                  Mc tin lng ng bo him x hi
                  <Typography variant="span" className="text-data">
                    {" "}
                    {item.money}
                  </Typography>
                </Box>
                <Box>
                  <Typography variant="span" className="text-data">
                    {item.money} x {item.inflationYear} x {item.stageMonth} ={" "}
                    {item.totalMonth.toLocaleString("vi", {
                      style: "currency",
                      currency: "VND",
                    })}
                  </Typography>
                </Box>
                {item.stageYear.map((value, index) => (
                  <Box key={index}>
                    <Box key={index}>
                      Giai on ng bo him x hi t:{" "}
                      <Typography className="text-data" variant="span">
                        T1 nm {value.value} n T12 nm {value.value}. Thi
                        gian 12 thng{" "}
                      </Typography>
                    </Box>
                    <Box>
                      Mc tin lng ng bo him x hi:{" "}
                      <Typography className="text-data" variant="span">
                        {item.money.toLocaleString("vi", {
                          style: "currency",
                          currency: "VND",
                        })}
                      </Typography>
                    </Box>
                    <Box>
                      <Typography className="text-data" variant="span">
                        {value.money} x {value.inflationValue} x {value.month} ={" "}
                        {value.total.toLocaleString("vi", {
                          style: "currency",
                          currency: "VND",
                        })}
                      </Typography>
                    </Box>
                  </Box>
                ))}
                <Box>
                  {" "}
                  Giai on ng bo him x hi t:{" "}
                  <Typography className="text-data" variant="span">
                    T1 nm {item.yearNext} n T{item.monthNext} nm{" "}
                    {item.yearNext}. Thi gian {item.monthNext} thng
                  </Typography>
                </Box>
                <Box>
                  Mc tin lng ng bo him x hi:{" "}
                  <Typography className="text-data" variant="span">
                    {item.money}
                  </Typography>
                </Box>
                <Box variant="span">
                  <Typography variant="span" className="text-data">
                    {item.money} x {item.inflationYearNext} x {item.monthNext} ={" "}
                    {item.totalMonthNext.toLocaleString("vi", {
                      style: "currency",
                      currency: "VND",
                    })}
                  </Typography>
                </Box>
              </>
            ) : (
              <>
                <Box>
                  {" "}
                  Giai on ng bo him x hi t:{" "}
                  <Typography className="text-data" variant="span">
                    T{item.month} n T{item.monthNext} nm {item.year}. Thi
                    gian {item.stageMonth} thng .
                  </Typography>
                  <Box>
                    Mc tin lng ng bo him x hi:{" "}
                    <Typography className="text-data" variant="span">
                      {item.money}
                    </Typography>
                  </Box>
                </Box>
                <Box>
                  <Typography className="text-data" variant="span">
                    {item.money} x {item.iflation} x {item.stageMonth} ={" "}
                    {item.total.toLocaleString("vi", {
                      style: "currency",
                      currency: "VND",
                    })}
                  </Typography>
                </Box>
              </>
            )}
          </Box>
        </Box>
      ))}
    </>
  );
}
export default ExplainStage;
